<template>
  <b-container>
      <h3 class="header center">TERMO DE RESPONSABILIDADE - REGRAS DE FREQUNCIA E CONDUTA</h3>
        <h5 class="center">Ao se inscrever no CURSINHO FOCUS da ASSOCIAO PAIDEIA, 
            o (a) aluno (a) assume a responsabilidade de cumprir e respeitar as seguintes normas:</h5>
        <ol class="terms-ol">
         <li>A Frequncia  obrigatria e condio de permanncia do (a) aluno (a) no curso preparatrio.</li>
         <li>O (A) aluno (a) que faltar nos dois primeiros dias de aula na primeira semana de curso ser automaticamente excludo.</li>
         <li>Tambm ser eliminado (a) o (a) aluno (a) que faltar duas vezes consecutivas ou alternadas na mesma semana, nas
sequncias sexta/segunda e emenda de feriados.</li>
         <li>O (a) aluno (a) ter direito a quatro faltas no semestre.</li>
         <li>Devido o curso preparatrio ser modalidade intensivo e de curta durao no sero admitidos quaisquer tipos de afastamento
temporrio como para viagens de lazer ou estudo, curso de CFC e outros eventos que concorram com os dias e horrio das aulas
do preparatrio. Essas faltas no sero abonadas e acarretaro no desligamento do (a) aluno (a) do preparatrio.</li>
         <li>Os afastamentos por doena ou trabalho sero abonados desde que notificados e justificados atravs de documentao legal.
(atestado mdico ou declarao em papel timbrado da empresa e com a assinatura do responsvel legal).</li>
         <li>O (A) aluno (a) que se inscrever a uma das vagas oferecidas e por motivos alheios no comunicar a sua desistncia perder o
direito de se reinscrever no preparatrio pelo perodo de um semestre letivo.</li>
         <li>Verificada a impossibilidade de frequentar ou continuar o curso preparatrio o aluno dever comunicar por escrito a sua
desistncia ao coordenador pedaggico.</li>
         <li> obrigao do (a) aluno (a): trazer o material escolar todos os dias, sobretudo o material didtico adotado pelo curso
preparatrio, comparecer s avaliaes, realizar os simulados presenciais e online e entregar feitos todos os exerccios propostos.</li>
         <li>A aquisio do material didtico  responsabilidade do aluno, sendo proibida a reproduo (cpia) do mesmo  art. 5o inciso
VII da Lei de Direitos Autorais e art. 184 do Cdigo Penal.</li>
         <li>O (A) aluno (a) regularmente matriculado ter 15 dias, contados a partir do primeiro dia de aula para entregar uma fotografia
recente tamanho (3x4) para o coordenador pedaggico para confeco de crach a ser utilizado no acesso  escola ou quando
solicitado.</li>
         <li> proibido ao aluno do preparatrio apresentar atitudes e comportamento incompatveis com o ambiente de aula. Qualquer
atitude que comprometa o bom andamento do preparatrio acarretar no cancelamento da matrcula e desligamento do aluno do
projeto. Portanto,  dever do aluno: observar o horrio de entrada e sada da sala de aula, manter-se em silncio durante as
explicaes do professor, respeitar os professores, colegas, funcionrios, diretores e demais pessoas que coabitam o espao
escolar.</li>
         <li> expressamente proibido o uso de celulares ou quaisquer outros aparelhos eletrnicos em sala de aula conforme Lei
Estadual no 12.730 de 11/10/2007.</li>
         <li>Preservar o patrimnio pblico dentro e fora do prdio escolar. No sero tolerados quaisquer danos e depredaes ao bem
pblico.  dever do aluno: preservar a limpeza dos ambientes (cho, banheiros, mesas do refeitrio, carteiras, paredes, etc.).
Preservar e proteger os trabalhos e atividades de alunos de outros perodos.</li>
         <li>Observar discrio ao se vestir, ao falar e ao agir no ambiente escolar.  expressamente proibido fumar no interior do prdio
bem como nas dependncias externas do mesmo (jardins e rea do estacionamento) conforme Lei Estadual no 13.541 de
07/05/2009.</li>
         <li>No se envolver em qualquer forma de violncia (agresso fsica ou psicolgica que denote racismo ou discriminao por
atuao partidria, orientao religiosa ou orientao sexual).</li>
         <li>Caso o (a) aluno (a) desista do preparatrio no ser feito nenhum tipo de devoluo ou reembolso quanto aos valores
referentes ao material didtico adquirido.</li>
         <li>O (A) aluno (a) fica comprometido a informar sua colocao e aprovao nos exames vestibulares e ENEM  coordenao
pedaggica do preparatrio que tem a incumbncia e obrigao de dar publicidade aos resultados alcanados, dado que  um
projeto de iniciativa social custeado e mantido pela administrao pblica do municpio.</li>
         <li>Os casos omissos deste termo sero submetidos  avaliao do Conselho de Professores e Direo do preparatrio que tem a
prerrogativa e a autonomia de agir visando ao bem comum e  tica.</li>
        </ol>
        <form @submit.prevent="clicked">
            <b-form-checkbox v-model="checkBox" id="terms" class="terms">
                Li e aceito os termos de responsabilidade.
            </b-form-checkbox>
            <br><br>
            <b-button :disabled="!checkBox" type="submit" class="user-button">
                Enviar
            </b-button>
        </form>
  </b-container>
</template>
<script>
export default {
    methods: {
        clicked: function() {
            this.$router.push({ name: 'BasicInfo'})
        },
    },
   data(){
       return {
       checkBox:false
       }
   },
   computed:{
       isEnabled(){
           return this.checkBox
       }
   }
}
</script>
<style lang="scss">
@import '../custom-bootstrap.scss';
.terms-ol{ 
    background-color: rgb(224, 224, 224);
    counter-reset: item;
    padding-right: 40px;
    padding-top: 20px;
    padding-bottom: 10px;
    border-radius: 8px;
 }
ol li { 
    display: block;
     margin-bottom: 10px;
    text-align: justify;
}
ol li:before {
    content: counter(item) ". ";
    counter-increment: item;
    font-weight: bold;
}
.header {
    padding: 15px;
    margin-bottom: 10px;
}
.center{
    font-weight: bold;
    text-align: center;
    margin: 10px 0 15px 0;
}
.terms{
    text-align: justify;
    float: right;
}
.terms:hover{
    cursor: pointer;
}
.user-button{
    padding: 10px 40px;
    display: block;
    margin: auto;
}
</style>



